@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home{display:flex;width:100%;height:100vh;flex-flow:column wrap;background-color:#313131;justify-content:space-between;align-items:center;padding-bottom:3rem;background-image:url(/static/media/bgImg.57d468e1.png);background-repeat:no-repeat;background-size:cover}.home_title{font-family:Montserrat;font-style:normal;font-weight:bold;font-size:68px;text-transform:uppercase;color:#61d9fb;align-self:flex-start;padding:10rem 0 1rem 7rem}.arrow:hover{cursor:pointer}.content{display:flex;width:100%;padding:1rem 300px;justify-self:center;background-color:#f4f4f4}.sidebar{position:-webkit-sticky;position:sticky;top:15px;width:20%;background-color:white;height:100%;margin-right:50px;padding:30px 20px;box-shadow:4px 4px 4px rgba(0,0,0,0.1),-4px 4px 4px rgba(0,0,0,0.1);justify-content:center}.sidebar_title{padding-bottom:30px;text-align:center;font-size:22px;text-decoration:underline;font-weight:bold}.sidebar_titles{margin-bottom:25px;font-size:18px}.sidebar_titles:hover{color:#61d9fb;cursor:pointer}.textbar{width:800px;background-color:white}.textbar .article{padding:30px 40px 30px 40px;white-space:pre-line;margin-bottom:15px;box-shadow:4px 4px 4px rgba(0,0,0,0.3),-4px 4px 4px rgba(0,0,0,0.3)}.textbar .article_title{font-style:normal;font-weight:bold;font-size:28px;line-height:29px;color:#23778f;padding-bottom:20px}.textbar .article_subtitle{font-family:Montserrat;font-style:normal;font-size:22px;padding:0 20px}.textbar .article_desc{text-align:justify}.textbar .article_img{width:100%}*{margin:0;clear:both;padding:0;box-sizing:border-box;font-family:"Montserrat", sans-serif}.redux{display:none}.scroll_to-top{position:fixed;display:flex;justify-content:center;align-items:center;bottom:40px;right:200px;width:40px;height:40px;border-radius:100%;border:0;background-color:#313131;z-index:1}.scroll_to-top--animation{animation:scrollTop 0.5s alternate ease infinite}@keyframes scrollTop{from{transform:translateY(2px)}to{transform:translateY(-1px)}}.scroll_to-top:hover{cursor:pointer}

